import React from "react";
 
const InfoAplicacion = () => {
  return (
    <div className="info-container">
    {/* Titre principal */}
    <h1 className="info-title"> propos de notre application</h1>

    {/* Section : Comment est-elle construite ? */}
    <section className="info-section">
      <h2>Comment notre application est-elle construite ?</h2>
      <p>
        Notre plateforme utilise la stack technologique <strong>MERN</strong>, qui associe 
        des technologies modernes pour offrir des performances optimales.
      </p>
      
      <ul className="info-list">
        <li><strong>Frontend - React.js :</strong> Exprience rapide et interactive avec Redux.</li>
        <li><strong>Backend - Node.js et Express.js :</strong> Gestion des requtes et scurit.</li>
        <li><strong>Base de donnes - MongoDB :</strong> Stockage efficace des proprits et utilisateurs.</li>
        <li><strong>Authentification - JWT :</strong> Protection des donnes avec des tokens de scurit.</li>
        <li><strong>Cloudinary :</strong> Gestion optimise des images des proprits.</li>
      </ul>
    </section>

    {/* Section : Comment fonctionne-t-elle ? */}
    <section className="info-section">
      <h2>Comment fonctionne notre application ?</h2>
      <p>
        Notre application est conue pour tre intuitive et efficace pour les acheteurs et les vendeurs.
      </p>
      
      <ul className="info-list">
        <li><strong>Inscription et Connexion :</strong> Accdez en toute scurit  votre compte.</li>
        <li><strong>Publication des Proprits :</strong> Ajoutez vos annonces avec photos et dtails.</li>
        <li><strong>Recherche et Filtres :</strong> Trouvez des proprits selon leur emplacement et prix.</li>
        <li><strong>Contact avec les Propritaires :</strong> Envoyez des messages directement depuis la plateforme.</li>
        <li><strong>Gestion du Profil :</strong> Modifiez vos informations et administrez vos publications.</li>
      </ul>
    </section>
  </div>
  );
};

export default InfoAplicacion;
